@import "_normalize";
@import "_variables";

html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body {
  color: $bodyTextColor;
  background: $bodyColor;
  font-family: Calibri, sans-serif;
}

h1 {
  margin: 0;
}

.Contener {
  max-width: 1000px;
  margin-top: 0;
  margin-right: auto;
  margin-left: auto;
  /* background-color: white;  */
}
p {
  text-align: justify;
  line-height: 2rem;
}
/* slider na górze strony  *********************************************************** */
.mainSlider {
  border: 10px;
  margin: 20px auto;
  max-width: 1000px;
}

.slides {
  overflow: hidden;
  position: relative;
  transition: height 0.4s;
  z-index: 0;
}

.slide {
  display: flex;
  min-height: 400px;
  left: 0;
  position: relative;
  top: 0;
  width: 100%;
  z-index: 0;
}

.js .slide {
  position: absolute;
}

.slide > figure {
  background-size: cover;
  background-position: center;
  bottom: 0;
  left: 0;
  margin: 0;
  opacity: 1;
  position: absolute;
  right: 0;
  top: 0;
  transition: opacity 0.4s;
}

.slide > figure img.hidden {
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: visible;
  position: static;
  padding: 0;
  width: auto;
}

.js .slide > figure img.hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: 1px;
  margin: -1px;
  overflow: hidden;
  position: absolute;
  padding: 0;
  width: 1px;
}

.slide-description {
  align-self: flex-end;
  padding: 64px 64px 64px 136px;
  position: relative;
  transition: opacity 0.4s;
  max-width: 612px;
  z-index: 0;
}

.slide-description:after {
  background-color: rgba(0, 0, 0, 0.6);
  bottom: 32px;
  content: "";
  left: 104px;
  position: absolute;
  right: 32px;
  top: 32px;
  z-index: -1;
}

.slide-description h3 {
  border-left: 6px solid #000;
  color: #fff;
  font-weight: 100;
  font-size: 24px;
  letter-spacing: 0.5px;
  line-height: 32px;
  margin: 0 0 16px 0;
  padding: 4px 0 0 24px;
}

.slide-description p {
  color: #fff;
  font-size: 16px;
  line-height: 24px;
  margin: 0;
  word-spacing: 1px;
}

.dots {
  bottom: 32px;
  display: none;
  font-size: 0;
  letter-spacing: 4px;
  position: absolute;
  right: 24px;
  z-index: 999;
  max-width: 200px;
}

.js .dots {
  display: block;
}

.dot {
  background-color: rgba(0, 0, 0, 0.6);
  border-radius: 50%;
  cursor: pointer;
  display: inline-block;
  height: 16px;
  margin-top: 8px;
  transition: background-color 0.6s ease, opacity 0.6s ease;
  width: 16px;
}

.dot:hover {
  background-color: #000;
}

.dots .active {
  background-color: #fff;
}

.dots .loading {
  opacity: 0;
}

.dots .active.error {
  background: #f22613;
}

.arrows {
  display: none;
}

.js .arrows {
  display: block;
}

.arrows .arrow-prev,
.arrows .arrow-next {
  background-color: rgba(0, 0, 0, 0.6);
  border: none;
  color: #fff;
  cursor: pointer;
  display: block;
  font-weight: bold;
  font-size: 16px;
  position: absolute;
  padding: 16px;
  top: 50%;
  transform: translate(0, -50%);
  transition: background-color 0.6s ease;
  z-index: 3;
}

.arrows .arrow-prev:hover,
.arrows .arrow-next:hover {
  background-color: rgba(0, 0, 0, 0.8);
}

.arrows .arrow-prev {
  left: 0;
}

.arrows .arrow-next {
  right: 0;
}

.first figure,
.first .slide-description {
  transition: unset;
}

.slides .slide.prev {
  z-index: 1;
}

.slides .active {
  z-index: 2;
}

.slides.zip figure {
  transform: translate(0, -100%);
}

.slides.zip .slide-description {
  transform: translate(0, 100%);
}

.slides.zip .active figure,
.slides.zip .active .slide-description {
  transform: translate(0, 0);
}

.slides.zip .active figure {
  transition: transform 0.4s ease;
}

.slides.zip .active .slide-description {
  transition: transform 0.4s 0.4s ease;
}

.slides.zip .prev figure,
.slides.zip .prev .slide-description {
  transition: transform 0.2s 0.8s;
}

.slides.chain figure {
  transform: translate(-100%, 0);
}

.slides.chain .slide-description {
  transform: translate(-100%, 0);
}

.slides.chain .active figure,
.slides.chain .active .slide-description {
  transform: translate(0, 0);
}

.slides.chain .active figure {
  transition: transform 0.4s ease;
}

.slides.chain .active .slide-description {
  transition: transform 0.4s 0.4s ease;
}

.slides.chain .prev figure,
.slides.chain .prev .slide-description {
  transition: transform 0.2s 0.8s;
}

.slides.fade .slide figure,
.slides.fade .slide .slide-description {
  transform: translate(0, 0);
}

.slides.fade .slide {
  opacity: 0;
}

.slides.fade .slide.active {
  opacity: 1;
  transition: opacity 0.4s;
  z-index: 2;
}

.slides.fade .slide.prev {
  transition: opacity 0.4s 0.4s;
  z-index: 1;
}

.slider-content {
  overflow: hidden;
  width: 100%;
}

.slides.tape {
  display: flex;
  flex: 0 1 auto;
  flex-direction: row;
  flex-wrap: nowrap;
  overflow: unset;
  margin: 0 auto;
  max-width: 1000px;
}

.slides.tape .slide {
  box-sizing: border-box;
  flex: 0 0 auto;
  flex-basis: 100%;
  max-width: 100%;
  overflow: hidden;
  position: relative;
  transition: margin-left 0.4s;
}

.slides.tape .slide.first {
  transition: unset;
}

.slides.tape .slide figure,
.slides.tape .slide .slide-description {
  opacity: 0.4;
  transition: opacity 0.4s;
}

.slides.tape .slide figure {
  bottom: -12px;
  left: -12px;
  right: -12px;
  top: -12px;
}

.slides.tape .slide.active figure,
.slides.tape .slide.active .slide-description {
  opacity: 1;
}

@media screen and (max-width: 980px) {
  .js .dots {
    display: none;
  }
  .slide {
    justify-content: center;
  }
  .slide-description {
    padding: 74px;
  }
  .slide-description:after {
    left: 50px;
    right: 50px;
  }
  .slide-description h3 {
    border-left: 0;
    padding: 0;
  }
}

/* ------------------------------------------------------------------------------------------------------ */

/* menu strony na calą szerokośc okna przeglądarki */
.menu {
  padding: 0;
  height: 150px;
  background-color: $menuColor;
  width: 100vw;
  position: sticky;
  top: 0;
  border-top: 10px;
}
.menu_Contener {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  max-width: 1000px;
  margin-top: 0;
  margin-right: auto;
  margin-left: auto;
}
@media screen and (max-width: 700px) {
  .menu_Contener {
    flex-direction: column;
  }
}

.menu_logo {
  max-width: 100%;
  display: flex;
}
.menu-logo--img {
  max-width: 150px;
  max-height: 150px;
}
.menu_link {
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  width: calc(100% - 150px);
}
/* menu navigation style********************************************************************* */
.navigation {
  background-color: $menuColor;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
  -ms-flex-pack: center;
  justify-content: center;
  position: -webkit-sticky;
  // min-height: 4rem;
  width: 100%;
}
.navigation__cont {
  width: 100%;
}
.navigation__cont--contUl {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: distribute;
  justify-content: space-around;
  padding: 0;
  margin: 0;
  -webkit-box-orient: horizontal;
  -webkit-box-direction: normal;
  -ms-flex-direction: row;
  flex-direction: row;
}
.navigation__cont--item {
  list-style-type: none;
  margin: 1.5rem 0.6rem;
}
.navigation__cont--item--a {
  display: block;
  -ms-touch-action: manipulation;
  touch-action: manipulation;
  text-decoration: none;
  color: $bodyTextColor;
  text-transform: uppercase;
  font-size: 1rem;
  padding: 1rem;
  -webkit-transition: border-bottom 0.8s ease-out;
  -o-transition: border-bottom 0.8s ease-out;
  transition: border-bottom 0.8s ease-out;
}
.navigation__cont--item--a:hover {
  color: $naviHover;
}
@media screen and (max-width: 700px) {
  .navigation__cont--item--a:hover {
    border-bottom: solid 1px #6cc8e9;
  }
}

.submenu {
  padding: 1rem;
}
@media screen and (max-width: 700px) {
  .menu_link {

    width: 100%;}
  .submenu {
    -webkit-padding-start: 0px;
    padding-inline-start: 0px;
  }
}
.submenu__item {
  list-style-type: none;
  padding-bottom: 5px;
  margin-bottom: 1rem;
  -webkit-transition: all 0.7s ease-out;
  -o-transition: all 0.7s ease-out;
  transition: all 0.7s ease-out;
}
.submenu__item:hover {
  border-bottom: solid 1px #6cc8e9;
}
.submenu__item--a {
  margin-bottom: 0.5rem;
  color: $bodyTextColor;
  text-transform: uppercase;
  font-size: 0.8rem;
  text-decoration: none;
  border-bottom: 1rem;
}
.submenu__item--a:hover {
  color: $naviHover;
}

ol > li > ul {
  display: none;
}

ol > li {
  height: 2rem;
  background-color: $menuColor;
}
@media screen and (max-width: 700px) {
  ol > li {
    height: auto;
  }
}

ol > li > ul > li {
  position: relative;
  height: auto;
}

ol > li:hover > .submenu {
  display: block;
  background-color: $menuColor;
  position: absolute;
  top: 3rem;
}
@media screen and (max-width: 700px) {
  ol > li:hover > .submenu {
    position: relative;
    height: auto;
    top: 0;
  }
}

.mainSwither__js {
  color: rgb(47, 104, 44);
  font-size: 3rem;
  padding: 0 1rem;
  -webkit-box-pack: end;
  -ms-flex-pack: end;
  justify-content: flex-end;
  display: none;
}
// pozostałe media queriss dla menu hamburger
@media screen and (max-width: 700px) {
  .navigation {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
    position: relative;
    width: 100%;
  }
  .navigation__cont {
    width: inherit;
  }
  .navigation__cont--contUl {
    display: none;
    -ms-flex-pack: distribute;
    justify-content: space-around;
    padding: 0;
    margin: 0;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-pack: center;
    -ms-flex-pack: center;
    justify-content: center;
  }
  .navigation__cont--contUl-visible {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
  .navigation__cont--item {
    text-align: center;
  }
  .navigation__cont--item--a {
    -ms-touch-action: manipulation;
    touch-action: manipulation;
    text-decoration: none;
    font-size: 1rem;
    padding: 0;
  }
  .navigation__cont--item--a:hover {
    color: #19adee;
  }
  .mainSwither__js {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
  }
}
